<script>
  import { onMount } from "svelte";
  import { userToken } from "../../store";
  let apps;

  onMount(async () => {
    await fetch("https://api-data-chellenge.herokuapp.com/app/all", {
      method: "GET",
      headers: {
        "auth-token": $userToken,
      },
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        apps = data;
      });
  });
</script>

{#if apps}
  <div class="row">
    {#each apps as app}
      <div class="col-6 m-auto">
        <h2>{app.title}</h2>
        <h3>{app.details}</h3>
      </div>
    {/each}
  </div>
{/if}
