<script>
  import { userToken } from "../../store";

  let phone;
  let password;

  const login = async () => {
    await fetch("https://api-data-chellenge.herokuapp.com/user/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        phone: phone,
        password: password,
      }),
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        $userToken = data.token;
      });
  };
</script>

<div class="row">
  <form class="col-6 m-auto" id="form">
    <div class="form-group">
      <label for="exampleInput1">Phone number</label>
      <input
        bind:value={phone}
        type="text"
        class="form-control"
        id="exampleInputEmail1"
        aria-describedby="emailHelp" />
      <small id="emailHelp" class="form-text text-muted">We'll never share your
        Phone number with anyone else.</small>
    </div>
    <div class="form-group">
      <label for="exampleInputPassword1">Password</label>
      <input
        bind:value={password}
        type="password"
        class="form-control"
        id="exampleInputPassword1" />
    </div>
    <div on:click={login} class="btn btn-primary">Submit</div>
  </form>
</div>
